<script lang="ts">
    import type { Props } from "$lib/utils/ui-framework";
    import Button from "./Button";

    let count = 0;

    // Because the react processor mechnaism (i.e. what attaches to the `react:` prefix below) does NOT type check the props,
    // we instead can/should use the `Props` utility type to ensure that the props we pass to the react component are satisfactory.
    const props: Props<typeof Button> = {
        onClick: () => (count += 1),
    };
</script>

<react:Button {...props} />

<p>Clicked {count} times</p>
